import React from 'react';
import './index.less';
import { Row, Card, Tooltip, Icon , Tag } from 'antd';
import { connect } from 'react-redux';
import {Link,withRouter} from 'react-router-dom';
import { TimesFun } from '../../utils';


class RightFrame extends React.Component {
    toAlbumIndex=()=>{
        window.open('https://photos.app.goo.gl/fGQ3LucAENRGJR199');
    }

    render() {
        return (
            <Row>
                <Card bordered={true} hoverable={true} className="card" cover={<img src={require('../../img/horseman.jpg')} onClick={()=>this.toAlbumIndex()} />}>
                    <div>
                        <p className="abstract1"> </p>
                    </div>


                </Card>
                <Card title="" hoverable={true} className="card">

                </Card>

            </Row>
        );
    }
}

const mapStateToProps = state => {
    return {
        issues: state.issues,
    }
}

export default withRouter(connect(mapStateToProps)(RightFrame));



