// 
export function TimeUpdate(time){
    if (!time) return '';
    let date = new Date(time);
    let Mouth = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
    let Dates = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
    return date.getFullYear() + '-' + Mouth + '-' + Dates;
}

// H5
export function ScrollToAnchor(){
    let anchorElement = document.getElementById('to-header');
    // id
    if (anchorElement) { anchorElement.scrollIntoView({ block: 'start', behavior: 'smooth' }); }
}


// 
export function TimesFun(timesData) {
    //
    let dateBegin = new Date(timesData.replace(/-/g, "/"));//-/new Date
    let dateEnd = new Date();//
    let dateDiff = dateEnd.getTime() - dateBegin.getTime();//
    let dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//
    let leave1 = dateDiff % (24 * 3600 * 1000)    //
    let hours = Math.floor(leave1 / (3600 * 1000))//
    if(hours<10){
        hours = '0' + hours;
    }
    //
    let leave2 = leave1 % (3600 * 1000)    //
    let minutes = Math.floor(leave2 / (60 * 1000))//
    if(minutes<10){
        minutes = '0' + minutes;
    }
    //
    let leave3 = leave2 % (60 * 1000)      //
    let seconds = Math.round(leave3 / 1000);
    if (seconds < 10) {
        seconds = '0' + seconds;
    }
    let timesString = '';

    timesString = dayDiff + '' + hours + '' + minutes + '' + seconds+'';

    return timesString
}


